import React from "react";
import ReactDOM from "react-dom";
import App from "./App.jsx";
import "./index.css";


ReactDOM.render(
    // <App/>,
    <App />,
    document.getElementById("root")
);

let pipePairs = document.querySelectorAll(".pipe-pair");
let bird = document.querySelector(".bird");
let fallInterval;
let gameOverInterval;
let ontop = document.querySelector(".on-top");
let btn = document.querySelector(".btn");
let score = 0;
let updateScoreInterval;
let playCount = 0;


let gameboard = document.querySelector(".game-board");

gameboard.addEventListener("click",jump);

//chainging the height of the up and down pipes after every iteration count
function changeHeight() {

    pipePairs.forEach((element) => {
        
        element.addEventListener("animationiteration", () => {


            let pipeUp = element.querySelector(".pipe-up");
            let pipeDown = element.querySelector(".pipe-down");

            let pipePairsHeight = element.getBoundingClientRect().height;

            let holeSize = parseInt(pipePairsHeight / 4) + 50;

            let pipeUpHeight = parseInt(Math.random() * (pipePairsHeight / 4)) + parseInt(holeSize / 2);
            let pipeDownHeight = pipePairsHeight - (pipeUpHeight + holeSize);

            pipeUp.style.height = pipeUpHeight + "px";
            pipeDown.style.height = pipeDownHeight + "px";  

            score++;
        })

    })
    
}


// falling down of bird 
function fall() {
    fallInterval = setInterval(() => {
        let birdTop = parseInt(window.getComputedStyle(bird).getPropertyValue("top"));

        bird.style.top = birdTop + 2 + "px";
    }, 10)
}

// jumping of bird
function jump() {
    let jumpCount = 0;
    let boardTop = gameboard.getBoundingClientRect().top;

    // console.log("inside jump function");
    bird.classList.add("jump");
    let jumpInterval = setInterval(() => {

        let birdTop = parseInt(window.getComputedStyle(bird).getPropertyValue("top"));
        if(birdTop > boardTop - birdTop)
        {
            bird.style.top = birdTop - 4 + "px";
            jumpCount++;
            if (jumpCount >= 20) {
                bird.classList.remove("jump");
                clearInterval(jumpInterval);
            }
        }
    }, 10);
}


function gameOver() {
    pipePairs.forEach(element => {
        let elementRight = window.getComputedStyle(element).getPropertyValue("right");
        element.style.animationName = "none";
        element.style.right = elementRight;
    })
    clearInterval(fallInterval);
    clearInterval(updateScoreInterval);
    
    bird.style.top = document.querySelector(".game-board").getBoundingClientRect().height / 2 + "px";
    score = 0;
     
    ontop.style.display = "flex";
    btn.innerText = "Play Again";
    
    // console.log("start game again");
    btn.addEventListener("click",()=>{
        
        pipePairs.forEach(element=>{
            element.style.right = -100 + "px";

        })
        
    
    });

}

function isGameOver()
{
    let birdRect = bird.getBoundingClientRect();
    let birdTop = birdRect.top;
    let birdBottom = birdRect.bottom;
    let birdRight = birdRect.right;
    let birdHeight = birdRect.height;

    let board = document.querySelector(".game-board");
    let boardBottom = board.getBoundingClientRect().bottom;

    
        pipePairs.forEach((element)=>{
            let pipeUp = element.querySelector(".pipe-up");
            let pipeDown = element.querySelector(".pipe-down");
    
            let pipeUpLeft = pipeUp.getBoundingClientRect().left;
            let pipeUpBottom = pipeUp.getBoundingClientRect().bottom;
    
            let pipeDownLeft = pipeDown.getBoundingClientRect().left;
            let pipeDownTop = pipeDown.getBoundingClientRect().top;
    
            if(((birdRight >= pipeUpLeft || birdRight >= pipeDownLeft) && (birdTop <= pipeUpBottom || birdBottom >= pipeDownTop)) || (birdTop + birdHeight >= boardBottom))
            {
                gameOver();
                // clearInterval(gameOverInterval);
            }
        })
    
    
}
function startGame() {
    // score = 0;
    playCount++;
    console.log(score);
    ontop.style.display = "none";

    pipePairs.forEach(element=>{
        element.style.animationName = "block-movement";
    })
    changeHeight();
    fall();
    updateScore();
    gameOverInterval = setInterval(isGameOver,10);
}

function updateScore(){

    let scoreDisplay = document.querySelector(".score-display");
    updateScoreInterval = setInterval(()=>{

        scoreDisplay.innerText = "Score : " + score/playCount;
    },10);
}


btn.addEventListener("click",startGame);








